import React from "react";

const Select = ({ name, currencies, value, label, valutaHandler }) => {
  return (
    <div className="form-component form-component-1 flex-30">
      <label htmlFor={name}>{label}</label>
      <br />
      <select
        className="inputField"
        name={name}
        value={value}
        onChange={valutaHandler}
      >
        {currencies.map(currency => (
          <option
            key={currency.shortName}
            value={currency.shortName}
            label={`${currency.name} (${currency.shortName})`}
          >{`${currency.name} (${currency.shortName})`}</option>
        ))}
      </select>
    </div>
  );
};

export default Select;
